.left{
    padding-top: 2rem;
    padding-bottom: 1rem;
    
    width: 40rem;
}
.right{
    width:20rem;
    padding-left: 0.8rem;
}

.up{
    display: flex;
    align-items: center;
   
    justify-content: space-around;
}
.amount-con{
    display: flex;
    justify-content: space-around;
    padding-top: 2rem;
    padding-bottom: 2rem;
}

.income{
    background: #FCF6F9;
                   border: 2px solid #FFFFFF;
                   box-shadow: 0px 1px 15px rgba(0, 0, 0, 0.06);
                   border-radius: 20px;
                   padding: 1rem;
                   p{
                                            font-size: 2.2rem;
                                            font-weight: 700;
                                         }
}


.history-con{
    grid-column: 4 / -1;
    h2{
        margin: 1rem 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .salary-title{
        font-size: 1.2rem;
        span{
            font-size: 1.8rem;
        }
    }
    .salary-item{
        background: #FCF6F9;
        border: 2px solid #FFFFFF;
        box-shadow: 0px 1px 15px rgba(0, 0, 0, 0.06);
        padding: 1rem;
        border-radius: 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        p{
            font-weight: 600;
            font-size: 1.6rem;
        }
    }
}
